@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');


.main {
  box-sizing: border-box;
  /* background-color: #B33922; */
  background: rgb(36,0,36);
  background: linear-gradient(135deg,rgb(100, 19, 147) 0%, rgb(41, 65, 184) 50%, rgba(0,212,255,1) 100%);
  color: rgba(255, 255, 255, 0.5);
  min-height: 100vh;
  /* border-color: #69533D; */
  /* border-width: 1em; */
  /* border-style: solid; */
}

#error {
  text-align: center;
  background-color: #B33922;
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
}

.errortext {
  font-size: 700%;
  margin-top: 20%;
}

.container {
  margin: 0px 15px;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  box-sizing: border-box;
}

.column {
  display: flex;
  flex-direction: column;
  flex: 1;
  box-sizing: border-box;
  flex-basis: 0%;
}

.left {
  text-align: right;
  margin-bottom: 1em;
  flex-basis: 30%;
  flex-grow: 0;
  position: sticky;
  top: 40%;
  align-self: flex-start;
  margin-right: 15px;
}

.right {
  margin-left: 15px;
  flex-basis: 50%;
  flex-grow: 0;
  width: 0;
}

.blankspace {
  margin: 30%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Inter', sans-serif;
  font-weight: 800;
  letter-spacing: -0.06em;
  line-height: 1;
  margin: 0;
}

ul {
  margin: 0;
}

li {
  margin: 0;
}

.title {
  font-size: 84px;
}

.subtitle {
  font-size: 32px;
}

.blurb {
  font-size: 24px;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
}

.year {
  font-size: 72px;
}

.image {
  width: 150px;
  display: block;
  margin-left: auto;
  margin-bottom: 15px;
  border-radius: 50%;
  border-color: #ffffff82;
  border-width: 2px;
  border-style: solid;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.2);
}

.aboutme {
  background-color: "rgba(255, 255, 255, 0.5)";
  box-shadow: "0px 20px 20px rgba(0, 0, 0, 0.4)";
  width: content;
}

.image::before {
  content: '';
  background: radial-gradient(ellipse at left top, rgba(255,255,255,0.35) 0%, rgba(255,255,255,0.15) 100%);
}

.githubIcon {
  width: 48px;
  padding-top: 15px;
  opacity: 50%;
}

.project-div {
  color: #1A1814;
  background: radial-gradient(ellipse at left top, rgba(255,255,255,0.35) 0%, rgba(255,255,255,0.15) 100%);
  padding: 30px;
  margin: 15px;
  transition: all 200ms linear;
  border-radius: 30px;
  border-width: 1px;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.2);
}

.project-button {
  opacity: 0%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.165);
  border: none;
  color: #1A1814;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  transition: all 50ms linear; 
  box-shadow:  5px 5px 10px #00000020,
              -5px -5px 10px #ffffff76,
              inset 0px 0px 0px #00000020,
              inset 0px 0px 0px #ffffff76;
  border-radius: 15px;
  margin-left: 30px;
}

.button-column {
  flex-basis: 20%;
  flex-grow: 0;
}

.content-parent {
  height: 0px;
  overflow: hidden;
  transition: height ease 400ms;
}

.main-image {
  width: 100%;
  height: auto;
  margin: 30px 0px;
}

.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 800,
  'GRAD' 0,
  'opsz' 24
}

span.size-48 {
  font-size: 48px;
  font-variation-settings: 'OPSZ' 48;
  transition: transform ease 400ms;
}

.project-content {
  width: 100%;
}

.each-slide-effect > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #1A1814;
  aspect-ratio: 16 / 9;
}

.each-slide-effect span {
  padding: 15px;
  font-size: 16px;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  background: #24242475;
  color: white;
  text-align: center;
  align-self: flex-end;
}

.nav:first-of-type {
  left: 15px !important;
}

.nav:last-of-type {
  right: 15px !important;
}

.default-nav {
  background: #24242475 !important;
  fill: white !important;
}

.default-nav:hover {
  background: #4e4e4e75 !important;
}

.react-slideshow-container {
  margin: 30px 0px;
}

/* For mobile. */
/* Not necessarily a small screen! */
@media (pointer:none), (pointer:coarse) {
  .project-button {
    opacity: 100%;
    padding: 5px;
  }
}

/* For desktop. */
/* Not necessarily big screens! */
@media (pointer:fine) {
  .project-div:hover {
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.4);
  }

  .project-div:hover .project-button {
    opacity: 100%;
  }

  .project-button:hover {
    background-color:rgba(255, 255, 255, 0.3);
    /* box-shadow:  10px 10px 15px #00000020,
                -10px -10px 15px #ffffff76,
                inset 0px 0px 0px #00000020,
                inset 0px 0px 0px #ffffff76; */
  }
}

/* For small widths */
/* Not necesserily mobile! */
@media only screen and (max-width: 1024px) {
  .left {
    flex-basis: 100%;
    position: static;
    margin-right: 0;
  }

  .image {
    margin-top: 100px;
  }

  .right {
    flex-basis: 40%;
    flex-grow: 1;
    margin-left: 0;
    width: 100%;
  }
  
  .blankspace {
    margin: 5%;
  }

  .project-div {
    margin-left: 15;
  }

  .row {
    flex-direction: column;
  }

  .project-button {
    margin-top: 15px;
    margin-left: 0px;
  }

  .each-slide-effect > div {
    aspect-ratio : 1 / 1;
  }
}